.App {
  background-color: whitesmoke;
  height: 100vh;
  display: flex;
  flex-direction: column;
  color: black;
  overflow: hidden;
}

.title {
  font-family: Cookie, cursive;
  font-weight: 400;
  font-Size: 50px;
  cursor: default;
  user-select: none;
}

.AppBar {
  background-color: #EE6E73
}

.AppContainer {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

.Graph {
  width: 100%;
  flex: 1;
  min-height: 0;
}

.Editor {
  background-color: #af4448;/**#282c34;**/
  color: white;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.EditorOptions {
  background-color: #ffa4a2;
  width: 100%;
  display: flex;
  justify-content: space-between;
  box-sizing: border-box;
  padding-left: 10px;
  padding-right: 10px;
  padding-bottom: 10px;
}

.EditorTimelineRow {
  display: flex;
  justify-content: flex-start;
  width: 100%;
  background-color: #af4448;
  margin-top: 5px;
}

.EditorTimelineContainer {
  margin-bottom: 5px;
  width: 100%;
}

.EditorPanel {
  background-color: whitesmoke;
  width: 100%;
  height: 15vh;
  color: black;
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding-bottom: 5px;
}

.EmptyEditorPanel {
  height: 15vh;
  padding-bottom: 5px;
}

.TrackControl {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #af4448;
  padding-left: 10px;
  padding-right: 10px;
}

.LoadModalFileZone {
  border: dashed #eeeeee 2px;
  border-radius: 2px;
  background-color: #fafafa;
  color: #bdbdbd;
  padding: 20px;
  cursor: pointer;
}

.modalActions {
  display: flex;
  justify-content: flex-end;
}

.settingsColumn {
  display: flex;
  flex-direction: column;
  width: 50%;
}

.aboutBorder {
  align-self: center;
  width: 50%;
  height: 230px;
  padding: 16px;
  border: solid grey 1px;
  border-radius: 4px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.EditorOutput {
  display: flex;
  align-items: center;
  width: 30%;
}

.EditorOutputError {
  color: red;
  font-size: 16px;
  font-weight: 500;
}

.trackTitle {
  width: 125px;
  margin-right: 10px;
}

.verticalDivider {
  border-top-width: 0;
  border-right-width: 1px;
  border-right-style: solid;
  height: 100%;
  margin-left: 5px;
  margin-right: 5px;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
